<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" width="600px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="success" dark v-bind="attrs" v-on="on">
          <v-icon left>create_new_folder</v-icon>
          <span>New Project</span>
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <h3>Add new project</h3>
        </v-card-title>
        <v-card-text>
          <v-form class="px-3">
            <v-text-field label="Title" v-model="title" prepend-icon="folder"></v-text-field>
            <v-textarea label="information" v-model="text" prepend-icon="edit"></v-textarea>

            <v-menu>
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  label="Due date"
                  v-bind="attrs"
                  v-on="on"
                  readonly
                  offset-y
                  prepend-icon="date_range"
                  :value="formattedDate"
                ></v-text-field>
              </template>
              <v-date-picker v-model="dueDate"></v-date-picker>
            </v-menu>

            <v-btn text class="success" @click="newProject">Create new Project</v-btn>
          </v-form>
        </v-card-text>
        <v-card-actions></v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import moment from "moment";
export default {
  props: ['projects'],  
  data() {
    return {
      dueDate: null,
      dialog: false,
      title: "",
      text: "",
      author: "Ahmed Sarhan",
      status: "Ongoing",
      dueDate: null
    };
  },
  methods: {
    newProject() {
      const project = {
        title: this.title,
        text: this.text,
        author: "Ahmed Sarhan",
        status: "Ongoing",
        due: moment(this.dueDate).format("Do MMMM YYYY")
      };
      this.projects.push(project);
      this.dialog = false;
    }
  },
  computed: {
    formattedDate() {
      return this.dueDate
        ? moment(this.dueDate).format("Do MMMM YYYY")
        : "pls enter a date";
    }
  }
};
</script>